// 初始化
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Inter, Noto Sans JP, Noto Sans TC;
}

::selection {
    color: rgb(241, 241, 241);
    background-color: rgb(0, 0, 0);
}

body {
    background-color: rgb(241, 241, 241);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    // background-image: url(https://images.unsplash.com/photo-1626931016224-32ec84776a65?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=676&q=80);
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    cursor: default;
}

.smooth-scroll-wrapper {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
}

@keyframes grain {
    0%,
    100% {
        transform: translate(0, 0);
    }
    10% {
        transform: translate(-5%, -10%);
    }
    30% {
        transform: translate(3%, -15%);
    }
    50% {
        transform: translate(12%, 9%);
    }
    70% {
        transform: translate(9%, 4%);
    }
    90% {
        transform: translate(-1%, 7%);
    }
}

body::after {
    pointer-events: none;
    z-index: 10000;
    content: "";
    position: fixed;
    width: 200vw;
    height: 200vh;
    left: -50%;
    top: -50%;
    background-image: url(film.png);
    opacity: .2;
    animation: grain .2s steps(6) infinite;
}

.ccb {
    top: 1000vw;
    left: 1000vh;
    pointer-events: none;
    position: fixed;
    z-index: 10000000;
    transition: .5s, top .5s, left .5s;
    transition-timing-function: cubic-bezier(0, -0.16, 0, 1);
    &-fill,
    &-outline {
        position: absolute;
        top: 0;
        left: 0;
        transition: .2s, filter .6s;
    }
    &-fill {
        filter: blur(0px);
        opacity: .2;
    }
    &-outline {
        opacity: 0;
        transform: scale(1.5);
        filter: blur(10px);
    }
    &.links {
        .ccb-outline {
            filter: blur(0px);
            opacity: 1 !important;
            transform: scale(1.1);
        }
        .ccb-fill {
            filter: blur(10px);
            // transition-delay: 1s;
            opacity: 0 !important;
            transform: scale(2);
        }
    }
}

.cca {
    top: 1000vw;
    left: 1000vh;
    pointer-events: none;
    position: fixed;
    z-index: 10000000;
    transition: .5s, top .05s, left .05s;
    transition-timing-function: ease-out;
    &.links {
        // opacity: 0;
    }
}

.fuck-u-windows {
    pointer-events: none;
    transition: 1s;
    &.close {
        opacity: 0;
        .frame {
            transform: translateY(100vh);
        }
    }
    &.get-out {
        display: none;
    }
    display: flex;
    position: fixed;
    z-index: 500;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(20px);
    background-color: rgba(255, 255, 255, 0.8);
    .frame {
        transition: 1.2s cubic-bezier(.96, .02, 0, .99);
        width: 80%;
        height: 80%;
        background-color: rgb(241, 241, 241);
        z-index: 600 !important;
        border: solid black 1.5px;
        margin: auto;
    }
}

// 基礎套件
h2 {
    font-size: 120px;
    line-height: 90%;
    font-weight: 500;
    z-index: 2 !important;
}

h3 {
    font-size: 60px;
    line-height: 90%;
    font-weight: 700;
    z-index: 2 !important;
}

h5 {
    font-weight: 300;
    font-size: 20px;
    letter-spacing: 2px;
    z-index: 2 !important;
}

p {
    font-size: 20px;
    z-index: 2 !important;
}

// 選單
.nav {
    height: 80px;
    width: 100%;
    padding: 20px 80px;
    background-color: rgb(241, 241, 241);
    // backdrop-filter: blur(20px);
    top: 0;
    left: 0;
    position: fixed;
    z-index: 100;
    &-logo {
        position: relative;
        font-size: 30px;
        font-family: Dela Gothic One !important;
    }
    &-list {
        position: absolute;
        right: 80px;
        height: 100%;
        // background-color: rgb(241, 241, 241);
        top: 0;
        bottom: 0;
        margin: auto;
        display: flex;
        align-items: center;
        &-item {
            font-size: 14px;
            text-transform: capitalize;
            margin: 20px;
            font-weight: bold;
            a {
                text-decoration: none;
                color: black
            }
        }
    }
}

.hero {
     ::selection {
        color: black;
        background-color: rgb(241, 241, 241);
    }
    position: relative;
    width: 100%;
    height: 800px;
    padding-top: 100px;
    background-color: yellowgreen;
    color: rgb(241, 241, 241);
    &-poster {
        width: 100%;
        max-width: 1400px;
        margin: auto;
        background-color: yellow;
        &-content {
            width: 100%;
            position: absolute;
            display: flex;
            flex-flow: column;
            // justify-content: center;
            height: 100%;
            top: 0;
            left: 0;
            padding-top: 100px;
            padding-left: 10%;
            h2 {
                text-transform: uppercase;
                width: 100%;
                max-width: 600px;
            }
            h5 {
                margin-top: 40px;
                margin-bottom: 20px;
                text-transform: uppercase;
            }
            a {
                color: rgb(241, 241, 241);
                font-size: 18px;
                position: absolute;
                bottom: 10%;
            }
        }
        img {
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover;
            object-position: center;
        }
    }
}

.intor {
    position: relative;
    width: 100%;
    height: 100%;
    // max-height: 800px;
    padding: 10vh 5%;
    &-left {
        text-transform: uppercase;
        position: absolute;
        left: 10%;
        h5 {
            font-weight: 500;
        }
    }
    &-center {
        font-size: 60px;
        max-width: 1000px;
        width: 100%;
        margin: auto;
        height: 100%;
        display: flex;
        flex-flow: column;
        justify-content: center;
        padding: 0 20px;
        padding-top: 40px;
        padding-bottom: 80px;
        // background-color: yellow;
    }
    &-right {
        text-transform: uppercase;
        position: absolute;
        right: 10%;
        padding-bottom: 20px;
        h5 {
            font-weight: 500;
        }
    }
}

.inv {
     ::selection {
        color: black;
        background-color: rgb(241, 241, 241);
    }
    position: relative;
    background-color: rgb(19, 19, 19);
    color: rgb(241, 241, 241);
    padding: 80px 0;
    padding-top: 160px;
    &-title {
        position: absolute;
        z-index: 10;
        right: 10vw;
        h2 {
            font-weight: 500 !important;
        }
    }
    &-content {
        padding-top: 80px;
        margin: auto;
        width: 100%;
        max-width: 1400px;
        h2 {
            font-size: 200px;
            text-transform: uppercase;
        }
        &-item {
            width: 100%;
            margin: 80px 0;
            position: relative;
            height: 400px;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            .img {
                transition: .5s ease-in-out;
                right: 0;
                width: 60%;
                height: 100%;
                transform-origin: right 0;
                // background-attachment: fixed;
                background-size: cover;
                background-position: center;
            }
            &-text {
                padding: 50px;
                width: 40%;
                z-index: 10;
                display: flex;
                flex-flow: column;
                p {
                    font-size: 20px;
                    padding: 40px 20px;
                }
            }
            &>p {
                font-size: 20px;
                padding: 50px;
                width: 40%;
            }
            &>h2 {
                padding: 50px;
                width: 40%;
                z-index: 10;
            }
        }
    }
}

.learning {
    padding: 120px 0;
    &-title {
        width: 100%;
        max-width: 1400px;
        display: block;
        margin: auto;
        margin-bottom: 60px;
        padding: 0 20px;
    }
    &-plans {
        width: 100%;
        max-width: 1400px;
        height: 100%;
        margin: auto;
        display: flex;
        &-item {
            margin: 0 20px;
            img {
                height: 400px;
                width: 100%;
                object-fit: cover;
                object-position: center;
            }
            &-text {
                h3 {
                    margin: 20px 0 !important;
                }
                margin-top: -50px;
                padding: 0 15px;
            }
        }
    }
}

.people {
    width: 100%;
    padding: 120px 0;
    &-title {
        width: 100%;
        max-width: 1400px;
        margin: auto;
        padding-bottom: 120px;
        display: flex;
        align-items: flex-end;
        p {
            width: 100%;
            max-width: 600px;
            padding-left: 10%;
        }
    }
    &-list {
        width: 100%;
        max-width: 1400px;
        margin: auto;
        display: flex;
        flex-flow: wrap;
        &-item {
            width: 100%;
            max-width: 400px;
            margin: 40px 20px;
            img {
                width: 100%;
                height: 500px;
                object-fit: cover;
                object-position: center;
            }
            &-title {
                margin-top: 20px;
                p {
                    font-weight: 500;
                    margin-bottom: 10px;
                }
            }
            &-des {
                margin-top: 20px;
            }
        }
    }
}

.footer {
     ::selection {
        color: black;
        background-color: rgb(241, 241, 241);
    }
    &-frame {
        position: relative;
        display: flex;
        align-items: center;
        padding: 40px;
        width: 60%;
        height: 100%;
    }
    position: relative;
    height: 800px;
    background-color: black;
    color: rgb(241, 241, 241);
    img {
        height: 100%;
        width: 40%;
        position: absolute;
        right: 0;
        top: 0;
        object-fit: cover;
        object-position: center;
    }
    &-menu {
        display: flex;
        flex-flow: column;
        justify-content: flex-start;
        margin: 20px;
        height: 90%;
        &-logo {
            font-size: 40px;
            font-family: Dela Gothic One !important;
            margin-bottom: 20px;
        }
        &-item {
            font-size: 20px;
            margin-bottom: 10px;
        }
    }
    &-content {
        display: flex;
        flex-flow: column;
        justify-content: flex-end;
        justify-items: flex-end;
        height: 90%;
        width: 400px;
        margin: 20px;
        &-title {
            margin-bottom: 40px;
            font-weight: 500;
        }
        &-des {}
    }
}


/*! locomotive-scroll v4.1.1 | MIT License | https://github.com/locomotivemtl/locomotive-scroll */

html.has-scroll-smooth {
    overflow: hidden;
}

html.has-scroll-dragging {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.has-scroll-smooth body {
    overflow: hidden;
}

.has-scroll-smooth [data-scroll-container] {
    min-height: 100vh;
}

[data-scroll-direction="horizontal"] [data-scroll-container] {
    height: 100vh;
    display: inline-block;
    white-space: nowrap;
}

[data-scroll-direction="horizontal"] [data-scroll-section] {
    display: inline-block;
    vertical-align: top;
    white-space: nowrap;
    height: 100%;
}

.c-scrollbar {
    position: absolute;
    right: 0;
    top: 0;
    width: 11px;
    height: 100%;
    transform-origin: center right;
    transition: transform 0.3s, opacity 0.3s;
    opacity: 0;
}

.c-scrollbar:hover {
    transform: scaleX(1.45);
}

.c-scrollbar:hover,
.has-scroll-scrolling .c-scrollbar,
.has-scroll-dragging .c-scrollbar {
    opacity: 1;
}

[data-scroll-direction="horizontal"] .c-scrollbar {
    width: 100%;
    height: 10px;
    top: auto;
    bottom: 0;
    transform: scaleY(1);
}

[data-scroll-direction="horizontal"] .c-scrollbar:hover {
    transform: scaleY(1.3);
}

.c-scrollbar_thumb {
    position: absolute;
    top: 0;
    right: 0;
    background-color: black;
    opacity: 0.5;
    width: 7px;
    border-radius: 10px;
    margin: 2px;
    cursor: -webkit-grab;
    cursor: grab;
}

.has-scroll-dragging .c-scrollbar_thumb {
    cursor: -webkit-grabbing;
    cursor: grabbing;
}

[data-scroll-direction="horizontal"] .c-scrollbar_thumb {
    right: auto;
    bottom: 0;
}